.cityContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  /* size */
  max-width: 60%;
  width: 40%;
  min-width: 320px;
  /* margin / padding */
  margin: 10px;
  margin-inline: auto;
  padding: 10px;
  /* bg adjust */
  /* From https://css.glass */
  background: rgba(255, 255, 255, 0.19);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.btnContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 48px;
}

.cityContainerClicked {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  /* margin / padding */
  margin: 10px;
  margin-inline: auto;
  /* size */
  max-width: 50%;
  width: auto;
  min-width: 350px;
  height:220px;
  /* From https://css.glass */
  background: rgba(255, 255, 255, 0.08);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(13.4px);
  -webkit-backdrop-filter: blur(13.4px);
  border: 1px solid rgba(255, 255, 255, 1);
}
